import arrow from '@/static/mbcImg/my/down-arrow.png';
import PT from '@/static/mbcImg/my/1.png';
import FW from '@/static/mbcImg/my/22.png';
import NI from '@/static/mbcImg/my/33.png';
export default {
	data() {
		return {
			arr:[],
			arrow:arrow,
			PT:PT,
			FW:FW,
			NI:NI,
			fold:true,
			id:'',
		};
	},
	computed: {
		
	},
	onLoad:function(val){
		console.log(val)
		this.id=val.id;
	},
	created() {
		this.cloud();
	},
	mounted() {},
	methods: {
		phone(){
			uni.makePhoneCall({
				phoneNumber: '4006182288' //拨打电话
			});
		},
		handleFold(){
			this.fold = !this.fold
		},
		cloud(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/cyd/info/'+ this.id +'?lookUserId=' + landRegistLG.user.id, //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.arr=response.data.content;
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			} else if (uni.getStorageSync('UUID')) {
				let UUID = uni.getStorageSync('UUID'); // 读取缓存的用户信息
				console.log(UUID);
				 // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/cyd/info/230?lookUserId='+ UUID, //接口地址。
					// data: params,
					method: 'GET',
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.arr=response.data.content;
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		}
	}
};
